/* base styles  */
html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

body {
  font-family: "arsenalregular", Arial, sans-serif;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0; }

h1, h2, h3, h4, h5, h6 {
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-weight: 100; }

h1, h2, h3, h4, h5, h6, p, strong, span, div, li, td {
  color: #121416; }

p {
  font-size: 14px;
  line-height: 1.3em;
  padding: 0;
  margin-bottom: .65em; }

p img {
  margin: 0; }

em {
  font-style: italic; }

b, strong {
  font-weight: bold;
  text-shadow: 0px 0px 1px rgba(50, 50, 50, 0.3); }

small {
  font-size: 80%;
  color: inherit; }

strong, span, em {
  color: inherit;
  line-height: inherit;
  font-size: inherit; }

b, strong {
  font-weight: bold; }

/*  LINKS  ///////////////////////////////////////////////////////////////////*/
a, a.link {
  color: #B49A65;
  font-family: "arsenalregular", Arial, sans-serif;
  display: inline-block;
  cursor: pointer;
  font-size: 13px;
  -webkit-transition: color 250ms ease-in-out;
  -moz-transition: color 250ms ease-in-out;
  -o-transition: color 250ms ease-in-out;
  transition: color 250ms ease-in-out; }

a, a.link, a:visited, a.link:visited {
  text-decoration: none;
  outline: 0; }

a:hover, a.link:hover, a:focus, a:focus.link {
  color: #451515; }

/*  ICONS  ///////////////////////////////////////////////////////////////////*/
.icon:before {
  display: inline-block;
  font: normal normal normal 24px / 1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 22px;
  -webkit-transition: all 150ms ease-out;
  -moz-transition: all 150ms ease-out;
  -o-transition: all 150ms ease-out;
  transition: all 150ms ease-out; }
  @media screen and (min-width: 788px) {
    .icon:before {
      font-size: 18px; } }

/*  LISTS  //////////////////////////////////////////////////////////////////*/
ul, ol {
  margin: 0;
  padding: 0; }

ul {
  list-style: none outside; }

ol {
  list-style: decimal; }

ul li {
  padding: 0;
  margin: 0;
  line-height: 1; }

/* NAV //////////////////////////////////////////////////////////////////////*/
nav {
  display: inline-block; }
  nav ul {
    list-style: none; }
    nav ul li {
      display: inline-block;
      float: left;
      padding: 5px 10px; }

/*  IMAGES  /////////////////////////////////////////////////////////////////*/
img {
  display: inline-block;
  max-width: 100%;
  height: auto; }

body {
  background-color: #1D173D;
  background-image: url("../images/bg/desktop.v3.jpg");
  background-position: center center;
  background-size: cover; }

/* STRUCTURE ////////////////////////////////////////////////////////////////*/
.container {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap; }

.panel {
  order: 1;
  flex: 0 1 980px;
  max-width: calc(100% - 40px);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }
  .panel .box {
    box-sizing: border-box; }
    .panel .box.story {
      order: 1;
      flex: 0 1 100%; }
      @media screen and (min-width: 788px) {
        .panel .box.story {
          flex: 0 1 50%; } }
    .panel .box.connect {
      order: 2;
      flex: 0 1 100%; }
      @media screen and (min-width: 788px) {
        .panel .box.connect {
          flex: 0 1 50%; } }

/* CONTENT ///////////////////////////////////////////////////////////////////*/
.panel {
  background-color: #fcf3d4;
  overflow: hidden;
  -webkit-box-shadow: 2px 6px 10px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 2px 6px 10px rgba(0, 0, 0, 0.3);
  box-shadow: 2px 6px 10px rgba(0, 0, 0, 0.3);
  -webkit-transition: all 500ms ease-out;
  -moz-transition: all 500ms ease-out;
  -o-transition: all 500ms ease-out;
  transition: all 500ms ease-out; }
  .panel:hover {
    -webkit-box-shadow: 2px 10px 25px rgba(0, 0, 0, 0.8);
